<template>
  <div class="login_center" @keydown.enter="submit">
    <div class="Login_box">
      <div class="Login_img">
        <img src="../assets/relax.jpg" alt=""/>
      </div>
      <el-form class="Login_form" :model="LoginForm" :rules="LoginRule" ref="loginForm">
        <el-form-item prop="username">
          <el-input prefix-icon="el-icon-user-solid" placeholder="用户名" v-model="LoginForm.username">
          </el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input prefix-icon="el-icon-lock" placeholder="密码" v-model="LoginForm.password" type="password">
          </el-input>
        </el-form-item>
        <el-form-item class="btns">
          <el-button type="primary" @click="submit" :loading="loading">登录</el-button>
          <el-button type="info" @click="disSubmit">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>

import {post} from '../network/request'

export default {
  name: "Login",
  data(){
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入密码'));
      }
      callback();
    }
    let validUser = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请输入用户名'));
      }
      callback();
    }

    return{
      result:[],
      loading:false,
      LoginForm:{
        username:'',
        password:'',
      },
      LoginRule:{
        username:[
          {validator:validUser, required:true,trigger: 'blur' },
          { min: 3, max: 6, message: '长度在 3 到 6 个字符', trigger: 'blur' }
        ],
        password: [
          {validator:validatePass,required:true,trigger:'blur'},
          {min:6,max:9,message:'6到9个字符之间',trigger:'blur'}
        ]
      },
    };
  },
  methods:{
    submit() {
      this.loading = true;
      post('login', this.LoginForm)
        .then(async res => {
          if (res.data.meta.status === 200) {
            this.$message.success("登陆成功");
            window.sessionStorage.setItem('token', res.data.data.token)
            await this.$router.push({path:"/admin",query:{value:this.LoginForm.username}});
          } else /*if (res.data.meta.status !== 200)*/ {
            this.$message.error("登录失败");
            return this.loading = false;
          }
 /*         console.log(res)*/
        }).catch(err => {
     /*   console.log(err)*/
      })
      setTimeout(()=>{
        return this.loading=false
      },3000)

    },
    disSubmit(){
      this.$refs.loginForm.resetFields();
      this.loading=false;
    },
  }
}
</script>
<style scoped>
.login_center{
  background-color:dimgray;
  height: 100%;
}
.Login_box{
  width: 400px;
  height: 300px;
  border-radius: 8px;
  background-color: #eee;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%,-50%);
}
.Login_img{
  padding: 10px;
  position: absolute;
  top: 25%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 80px;
  height: 80px;
  /*  box-shadow: 0 0 10px #ddd;*/
}
img{
  z-index: 999;
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.Login_form{
  position: absolute;
  bottom: 0;
  padding: 0 20px;
  box-sizing: border-box;
  width: 100%;
}
.btns{
  display: flex;
  justify-content:center;
}
</style>



