import axios from "axios";

const instance=axios.create({
  baseURL:'http://8.140.107.62:8890/api/private/v1/',
  timeout:10000,
})
const instance1=axios.create({
  baseURL:'http://8.140.107.62:8081/user/',
  timeout:10000,
})
instance.interceptors.request.use(config=>{
 /* console.log(config)*/
  config.headers.Authorization=window.sessionStorage.getItem('token')
  return config
}),
  err=>{
    return Promise.reject(err)
  }
export function post(url,data){
  return instance.post(url,data)
}
export function get(url,params){
  return instance.get(url,params)
}
export function post1(url,data){
  return instance1.post(url,data)
}
export function get1(url,params){
  return instance1.get(url,params)
}




