<template>
  <el-container class="main">
    <el-header>
      <img src="../../assets/管理员.png" alt=""><strong><span>{{name}}</span></strong>
      <div class="but">
        <el-button type="danger" @click="LoginOut">退出</el-button>
      </div>
    </el-header>
    <el-container>
      <el-aside width="175px">
        <el-menu background-color="#545c64" text-color="#fff" router>
          <el-menu-item index='/users'>
           <div class="el-icon-user"> <span slot="title">用户管理</span></div>
          </el-menu-item>
          <el-menu-item index="/goods">
          <div class="el-icon-lock"><span slot="title">权限管理</span></div>
          </el-menu-item>
          <el-menu-item index="/Authority">
           <div class="el-icon-s-goods"> <span slot="title">商品管理</span></div>
          </el-menu-item>
          <el-menu-item index="/Order">
            <div class="el-icon-platform-eleme"><span slot="title">订单管理</span></div>
          </el-menu-item>
        </el-menu >
      </el-aside>
      <el-main>
      <router-view></router-view>
      </el-main>
    </el-container>
  </el-container>
</template>

<script>
export default {
  name: "admin",
  data(){
    return{
      name:this.$route.query.value,
    }
  },
  methods:{
    LoginOut(){
      localStorage.removeItem('token');
      this.$router.push("/Login")
    }
  }
}
</script>

<style scoped>
.el-header{
  line-height: 60px;
  display: flex;
  justify-content: space-between;
  background-color: #B3C0D1;
}
.el-aside {
  background-color: #D3DCE6;
}
.el-main {
  background-color: #E9EEF3;
}
.main{
  height: 100%;
}
img{
  width: 50px;
  height: 50px;
}
.el-menu{
  height: 100%;
}
.el-menu-item{
  margin-top: 50px;
}
.el-menu-item div span{
  padding-left: 10px;
}
</style>



