<template>
  <div id="add">
    <el-form  :model="form" label-width="80px" :rules="rules" ref="from" @keydown.enter.native="onSubmit">
      <el-form-item label="用户名" prop="username">
        <el-input v-model="form.username" placeholder="请输入用户名" ></el-input>
      </el-form-item>
      <el-form-item label="密码" prop="password">
        <el-input v-model="form.password" placeholder="请输入密码"></el-input>
      </el-form-item>
      <el-form-item label="qq" prop="qq">
        <el-input v-model="form.qq" placeholder="请输入qq"></el-input>
      </el-form-item>
      <el-form-item label="地址" prop="address">
        <el-input v-model="form.address" placeholder="地址"></el-input>
      </el-form-item>
      <el-form-item label="备注" prop="remarks">
        <el-input v-model="form.remarks" placeholder="备注"></el-input>
      </el-form-item>
      <el-form-item class="B">
        <el-button type="primary" @click="onSubmit">提交</el-button>
        <el-button type="danger" @click="out">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import {post1} from "../network/request";

const Qs = require('qs');
export default {
  name: "Add",
  data() {
    return {
      form: {
        username: '',
        password:'',
        qq:'',
        address:'',
        remarks:'',
      },
      rules:{
        username: [
          { required: true, message: '请输入用户名', trigger: 'blur' },
          { min: 1, max: 5, message: '长度在 1 到 5 个字符', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '请输入密码', trigger: 'blur' },
          { min: 3, max: 9, message: '长度在 3 到 9 个字符', trigger: 'blur' }
        ],
        qq: [
          { required: true, message: '请输入qq', trigger: 'blur' },
          { min: 6, max: 11, message: '长度在 6 到 11 个字符', trigger: 'blur' }
        ],
        address: [
          { required: true, message: '请输入地址', trigger: 'blur' },
          { min: 1, max: 999, message: '长度在 1 到 999 个字符', trigger: 'blur' }
        ],
        remarks: [
          { required: false, message: '备注', trigger: 'blur' },
          { min: 1, max: 999, message: '长度在 1 到 999 个字符', trigger: 'blur' }
        ],
      }
    }
  },
  methods: {
    onSubmit() {
      let str={"username":this.form.username,"password":this.form.password,"qq":this.form.qq,"address":this.form.address,"remarks":this.form.remarks}
      console.log(Qs.parse(str))
      post1("insert",Qs.parse(str)).then(res=>{
        if (res.data.status===200){
          this.$message.success("添加成功!");
          console.log(res);
          this.$router.push({path:"/users",query:{date:res.data.message,username:this.form.username,password:this.form.password,qq:this.form.qq,address:this.form.address,remarks:this.form.remarks}});
        }else if (res.data.status===28004){
          this.$message.error("用户名已被注册!");
          this.$router.push("/Add");
        }else {
          this.$message.error("添加失败!")
          window.location.reload();
        }
      })
      /*      console.log(Qs.parse(str));*/
    },
    out(){
      this.$router.push("/users")
    }
  }
}
</script>

<style scoped>
.el-form{
  margin:60px 60px ;
}
</style>



