import Vue from 'vue'
import Router from 'vue-router'
import Login from "../components/Login";
import admin from "../view/Admin/admin";
import users from "../view/Users/users";
import Authority from "../view/Authority/Authority";
import goods from "../view/Goods/goods";
import Orders from "../view/Orders/Orders";
import Add from "../components/Add";

Vue.use(Router)

const router=new Router({
  routes: [
    {
      path: '/',
      redirect:"/Login",
    },
    {
      path:'/login',
      component:Login,
      meta:{
        title:"首页"
      }
    },
    {
      path:"/admin",
      component:admin,
      redirect:"/users",
      meta:{
        title: "Admin"
      },
      children:[
        {path:"/users",component:users,meta:{title:"用户列表"}},
        {path:"/goods",component:goods,meta:{title:"商品列表"}},
        {path:"/Authority",component:Authority,meta:{title:"权限管理"}},
        {path:"/Order",component:Orders,meta:{title:"订单管理"}},
        {path:"/Add",component:Add,meta:{title:"添加用户"}}
      ]
    }
  ]
})
export default router
router.beforeEach((to,from,next)=>{
  window.document.title = to.meta.title;
  if (to.path=== '/login') return next();
  //获取token
  const tokenStr =window.sessionStorage.getItem('token');//获取token
  if(!tokenStr) return next('/login')
  next()
})




