<template>
<div id="users">
  <el-card>
    <el-row :gutter="15">
      <el-col :span="8">
        <el-input placeholder="请输入内容" v-model="queryInfo.search" class="search" @keydown.enter.native="getUserlist">
          <el-button slot="append" icon="el-icon-search" @click="getUserlist"></el-button>
        </el-input>

      </el-col>
      <el-col :span="4">
        <el-button type="primary" @click="submit" class="add">添加用户</el-button>
      </el-col>
    </el-row>
  </el-card>
  <el-table
    :data="userlist" stripe @row-click="getDetails">
     <el-table-column prop="date" label="日期"></el-table-column>
     <el-table-column prop="username" label="用户名"></el-table-column>
     <el-table-column prop="password" label="密码"></el-table-column>
     <el-table-column prop="address" label="地址" width="400px"></el-table-column>
    <el-table-column prop="qq" label="qq"></el-table-column>
     <el-table-column prop="remarks" label="备注"></el-table-column>
    <el-table-column label="操作" width="150px">
      <template slot-scope="scope">
        <el-dialog
          title="修改用户"
          :visible.sync="editDialogVisible"
          width="40%" :showClose="false"
        @keydown.enter.native="edit">
          <el-form :model="editform"  label-width="100px" >
            <el-form-item label="用户名" class="updateitem">
              <el-input v-model="editform.username" disabled></el-input>
            </el-form-item >
            <el-form-item label="密码" class="updateitem">
              <el-input v-model="editform.password"></el-input>
            </el-form-item >
            <el-form-item label="qq" class="updateitem">
              <el-input v-model="editform.qq"></el-input>
            </el-form-item >
            <el-form-item label="地址" class="updateitem">
              <el-input v-model="editform.address"></el-input>
            </el-form-item >
            <el-form-item label="备注" class="updateitem">
              <el-input v-model="editform.remarks"></el-input>
            </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer">
    <el-button @click.stop="editDialogVisible = false">取 消</el-button>
    <el-button type="primary" @click.stop="edit">确 定</el-button>
  </span>
        </el-dialog>
        <el-button type="primary" icon="el-icon-edit" circle @click.native.stop="Update(scope.row)"></el-button>
        <el-button type="danger" icon="el-icon-delete" circle @click.native="Delete"></el-button>
      </template>
    </el-table-column>
  </el-table>
  <el-pagination
    @size-change="handleSizeChange"
    @current-change="handleCurrentChange"
    :current-page="queryInfo.pagenum"
    :page-sizes="[1, 2, 5, 10]"
    :page-size="queryInfo.pagesize"
    layout="total, sizes, prev, pager, next, jumper"
    :total="100">
  </el-pagination>
</div>
</template>

<script>
import axios from "axios";
import {get, post, post1} from "../../network/request";
import Qs from "qs";
export default {
  name: "users",
  data(){
    return{
      editDialogVisible:false,
      queryInfo:{
        pagenum:'',
        pagesize:'',
       search:'',
    },
      editform:'',
      msgname:"",
      editlist:'',
      userlist:[],
    }
  },
  created() {
    this.getUserlist()
  },
  methods: {
    submit() {
      this.$router.push("/Add");
    },
   Update(row){
     this.msgname=row.username;
/*     console.log(row)*/
    axios({
      method: 'get',
      url: 'http://8.140.107.62:8081/user/queryByName',
      params: {
        username: this.msgname,
      }
    }).then(res=>{
      if (res.data.status===200){
        this.editform=res.data.meta.data
      }else{
        this.$message.error("获取失败!")
      }
    /*  console.log(res)*/
      })
      this.editDialogVisible=true;
    },
   async edit(){
      let str={"username":this.editform.username,"password":this.editform.password,"qq":this.editform.qq,"address":this.editform.address,"remarks":this.editform.remarks}
     await post1("update",Qs.parse(str)).then(res=>{
        if (res.data.status!==203){
          return this.$message.error("修改失败!")
        }
        this.$message.success("修改成功!")
        this.editDialogVisible=false
        this.getUserlist();
      })
    },
    getDetails(row){

   /*   console.log(this.msgname)*/
    },
    async getUserlist() {
      let S={"search":this.queryInfo.search,"pagenum":this.queryInfo.pagenum,"pagesize":this.queryInfo.pagesize}
      axios({
        method:'get',
        url:'http://8.140.107.62:8081/user/query/',
        timeout:10000,
        params:{
          search:this.queryInfo.search,
          pagenum:this.queryInfo.pagenum,
          pagesize:this.queryInfo.pagesize
        }
      }).then(res=>{
        let D=res.data.status;
        switch (D){
          case 500:
            this.$message.error("查询失败!")
        }
        this.userlist =res.data.meta.data;
     /*   console.log(res.data)*/
      })
    },
   async Delete(){
   const confirmResult=await this.$confirm('您确认要删除吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err=>err)
     if (confirmResult!=="confirm"){
       return this.$message.success("取消成功!")
     }else {
         axios({
      method:'get',
      url:'http://8.140.107.62:8081/user/delete',
      timeout:10000,
      params:{
        username:this.msgname
      }
    }).then(res=>{
           if (res.data.status === 200) {
             this.$message.success("删除成功!");
             this.getUserlist();
           } else{
             this.$message.error("删除失败!");
           }
         })
     }
    },
    handleSizeChange(NewSize) {
      this.queryInfo.pagesize=NewSize;
      this.getUserlist();
    },
    handleCurrentChange(NewPage) {
     this.queryInfo.pagenum=NewPage;
     this.getUserlist()
    }
  }
}
</script>

<style scoped>

</style>



